@media (max-width: 1439px) {
  .menu-nav--closed .menu-nav__btn::before {
    content: "";
    position: absolute;
    top: 25px;
    right: 20px;
    width: 24px;
    height: 2px;
    box-shadow: 0 8px 0 0 #ffffff, 0 16px 0 0 #ffffff, 0 24px 0 0 #ffffff; } }

@media (min-width: 1439px) {
  .menu-nav--closed .menu-nav__wrapper {
    position: relative; } }

@media (max-width: 1439px) {
  .menu-nav--opened .menu-nav__btn::before, .menu-nav--opened .menu-nav__btn::after {
    content: "";
    position: absolute;
    right: 23px;
    width: 25px;
    height: 2px;
    background-color: #ffffff; }
  .menu-nav--opened .menu-nav__btn::before {
    transform: rotate(45deg); }
  .menu-nav--opened .menu-nav__btn::after {
    transform: rotate(-45deg); } }

.menu-nav__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
