    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center; } }

.menu-nav__link {
  padding: 14px 20px; }

.page-header__basket {
  margin-left: auto;
  padding: 10px; }
  .page-header__basket:hover {
